import React from 'react';
//import artwork from './PORTFOLIO_STORY.png';
import portfolio from './portfolio.mp4'
import './home.styles.scss'


import Avatar from '../../comps/avatar/avatar.comp';
import {Player, ControlBar, LoadingSpinner} from 'video-react';
import '../../../node_modules/video-react/dist/video-react.css'; 

import {Animated} from "react-animated-css";

class Home extends React.Component {
    
    constructor(props, context) {
        super(props, context)



        this.toggleFullscreen = this.toggleFullscreen.bind(this);
        this.play = this.play.bind(this);
        }

        toggleFullscreen() {
            this.player.toggleFullscreen()
        }

        play(){
            this.player.play()
        }

        toggleFullscreen2() {
            this.player.play()
            this.player.toggleFullscreen()
        }

        

    render() {
      
        return (
            <div className="container">
                <div className='mobile-msg'>
                <h1>Looking for a content creator? 
                Your search is over.<br/><br/>
                Discover my bio in video!</h1>
                <button className="ghost-button" onClick={this.toggleFullscreen2}>Tap to see</button>
                <div id="video-mobile">
                <Player 
                loop={true}
                ref={player => { this.player = player;}}
                fluid={false} 
                width={0}
                autoPlay={true}
                muted
                playsinline
                src={portfolio}>
                <LoadingSpinner />
                <ControlBar disableCompletely={true}/>
                </Player>
                </div>
                </div>
                <div className="img-container" onDoubleClick={this.toggleFullscreen}>
                    <Player 
                    loop={true}
                    ref={player => { this.player = player;}}
                    className="test"
                    fluid={false}
                    
                    muted
                    playsinline
                    src={portfolio}>
                    <LoadingSpinner />
                    <ControlBar disableCompletely={true}/>
                    </Player>
                </div>
                <Animated animationIn="fadeInLeft" isVisible={true} animationInDuration={1000} animationOut="zoomOutDown"> 
                <Avatar/>
                </Animated>
            </div>
        )
    }
}

export default Home;