import React from 'react';
import './App.css';

import { Switch, Route } from 'react-router-dom'

import Home from './pages/home/home.comp'
import Bio from './pages/bio/bio.comp'
import Contact from './pages/contact/contact.comp'
import Header from './comps/header/header.comp'
import Portfolio from './pages/portfolio/portfolio.comp'

import Logo from './comps/logo/logo.comp';

function App() {
  return (
    <div className="App">
    <Logo className='logo'/> 
      <Switch>
        <Route exact path='/hiremeascontentcreator' component={Home}/>
        <Route path="/hiremeascontentcreator/bio" component={Bio}/>
        <Route path="/hiremeascontentcreator/contact" component={Contact}/>
        <Route path="/hiremeascontentcreator/portfolio" component={Portfolio}/>
      </Switch>
      <Header/>
    </div>
  );
}

export default App;
